.itemIcon {
    width: 100%;
    height: 100%;
    object-fit: contain;
}

.itemIconStatic {
    z-index: 1;
}

.itemIconDragged {
    z-index: 10;
    position: relative;
}

.itemIcon:hover {
    cursor: move; /* Change cursor to pointer on hover */
}

.itemQuantity {
    position: absolute;
    bottom: 0;
    right: 0;
    background-color: rgba(0, 0, 0, 0.3);
    color: white;
    padding: 2px 4px;
    border-radius: 2px;
    font-size: 12px;
    pointer-events: none; /* Zapobiega interakcji z licznikiem */
}
