@import 'variables.css';

.containerBackground {
    background-image: url("../../Images/Backgrounds/MonsterBackgroundDark.jpg");
    background-size: cover; 
    background-repeat: no-repeat;
    background-attachment: fixed; 
    
    position: absolute;
    left: 50%;
    transform: translate(-50%, 0); 

    min-height: 100%; 
    min-width: 100%; 
}

* {
    font-family: var(--font-family);
}

.container {
    background-color: var(--light--grey--background-color); /* Gray semi-transparent background */

    display: flex;
    justify-content: center; /* Center horizontally */
    
    position: absolute;
    left: 50%;
    transform: translate(-50%, 0); /* Center horizontally, no vertical shift */
    
    padding: 10px;

    min-height: 70%; /* Adjust the height as needed */
    min-width: 60%; /* Adjust the width as needed */

    border-radius: 15px;
}

.contentContainer {
    background-color: var(--dark--grey--background-color);
    text-align: center;

    padding: 20px;

    width: 100%; /* Make it fully fill the .container horizontally */

    border-radius: 15px;
}

.contentContainer h1 {
    font-size: 32px;
    margin-bottom: 10px;
    color: #333;
}

.contentContainer p {
    font-size: 18px;
    color: #555;
}

.worldContainer {
    background-image: linear-gradient(rgba(0, 0, 0, 0.3), rgba(0, 0, 0, 0.3)), url('../../Images/Backgrounds/MonstersHunt/NearbyLands.png');
    background-size: cover; /* Adjust as needed (cover, contain, etc.) */
    background-repeat: no-repeat; /* Prevent image repetition */
    background-position: center center; /* Center the image horizontally and vertically */
    
    width: 320px;
    height: 550px;

    padding: 20px;

    border-radius: 15px;
}
.worldContainer h1{
    color: var(--white-font-color);
}

.buttonContainer {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.button {
    background-color: var(--dark--black--background-color);
    
    width: 95%;
    height: 41px;

    margin: 3px;

    border: none;
    border-radius: 4px;

    font-size: 16px;
    color: var(--white-font-color);
    
    cursor: pointer;
}

.button:hover {
    background-color: #181818; /* Change to your desired hover color */
}

.button.locked {
    background-color: grey;
    cursor: not-allowed;
    opacity: 0.5;
}

.button.locked:hover {
    background-color: grey; /* Ensure the hover effect doesn't change for locked buttons */
}