@import 'variables.css';

.containerBackground {
    background-image: url("../../Images/Backgrounds/KnightBackgroundDark.jpg");
    background-size: cover;
    background-repeat: no-repeat;
    background-attachment: fixed;
    
    position: absolute;
    left: 50%;
    transform: translate(-50%, 0); /* Center horizontally, no vertical shift */

    min-height: 100%; /* Adjust the height as needed */
    min-width: 100%; /* Adjust the width as needed */
}

    .containerHeroView {
        background-color: var(--light--grey--background-color);

        padding: 10px;
        width: 1356px;
        height: 792px;

        margin: 0 auto; /* Center the container horizontally */
        border-radius: 10px;
    }

        .containerHeroHorizontal {
            color: var(--white-font-color);
            font-family: var(--text-font), var(--text-font-family);

            display: flex;
            justify-content: center;

            border-radius: 10px;

            margin-bottom: 10px;
        }

        .containerHeroHorizontalTop {
            width: 1336px;
            height: 60px;
        }

            .containerHeroExp {
                background-color: var(--light--black--background-color);
                font-size: 40px;
                border-radius: 10px;
                border: 1px solid black;
                margin-right: 4px;
                width: 298px;
            }
                
                .expBar {
                    overflow: hidden;
                    width: 296px;
                    height: 58px;
                    border-radius: 10px;
                    position: relative; /* Add this to establish a positioning context */
                }
                
                .expFill {
                    background-color: #5590ff93;
                    width: 50px; /* Adjust the width of the filled area */
                    height: 58px;
                    border-radius: 10px;
                    position: absolute; /* Position the filled area relative to the .expBar */
                }
                
                .expText {
                    text-align: center;
                    padding: 10px;
                    font-size: 40px;
                    line-height: 40px;
                    position: absolute; /* Position the text relative to the .expBar */
                    width: 100%; /* Ensure the text spans the full width of .expBar */
                    top: 0; /* Position text at the top of .expBar */
                }
            
            .containerHeroName{
                background-color: var(--light--black--background-color);

                font-size: 40px;
                
                border-radius: 10px;
                border: 1px solid black;

                margin-right: 4px;
                
                width: 468px;
            }

                .containerHeroName h2{
                    text-align: center;
                    
                    padding: 10px;
                    font-size: 40px;
                    line-height: 40px;
                }

            .containerCurrency {
                background-color: var(--light--black--background-color);    
                border-radius: 10px;
                border: 1px solid black;
                width: 562px;
                display: flex;
                justify-content: space-between; /* Distribute items with space in between */

                padding-left: 100px;
                padding-right: 100px;
            }
                    
                .currencyItem {
                    display: flex; /* Display items horizontally */
                    align-items: center; /* Center vertically */
                }

                .goldIcon,
                .diamondIcon {
                    border-radius: 20px;
                    border: 1px solid black;
                    width: 55px; /* Adjust the width and height for your icons */
                    height: 55px;
                    margin-right: 10px; /* Add spacing between the icon and the amount */
                }
                
                .currencyAmount {
                    font-size: 24px; /* Adjust the font size for the currency amount */
                }
            
        .containerHeroHorizontalBottom {
            width: 1336px;
            height: 702px;
        }

            .statsCanvas {
                background-color: var(--light--black--background-color);
                
                padding: 15px;

                color: var(--white-font-color);
                display: flex;
                flex-direction: column;

                border-radius: 10px;
                border: 1px solid black; /* Set the border color for monsterContainer to red */

                width: 298px;
            }

            .statsCanvas h3{
                text-align: center;
                font-size: 40px;
            }

            .stat {
                display: flex;
                justify-content: space-between; /* Create space between name and value */
                margin-bottom: 0px; /* Adjust this margin as needed for spacing */
                font-size: 20px;
            }

            .statName {
                text-align: left; /* Align the name to the left */
            }

            .statValue {
                text-align: right; /* Align the value to the right */
            }

            .equipCanvas {
                background-image: url('../../Images/HeroScreen/HeroDark.png');
                background-size: cover;
                
                margin-left: 4px; /* Adjust margin */
                margin-right: 4px; /* Adjust margin */

                border-radius: 10px;
                border: 1px solid black; /* Set the border color for monsterContainer to red */

                width: 468px;
                height: 702px;
            }

            .slotsHolder {
                position: relative; /* Add relative positioning to the equipCanvas */
            }

            .itemSlot {
                background-color: rgba(27, 27, 27, 0.603);
                border: 1px solid white;
                position: absolute; /* Set the item slot as absolute positioning */
            }

            /* Extend the common class for different sizes */
            .bigItemSlot {
                width:  142px;
                height: 212px;
            }

            .mediumItemSlot {
                width:  142px;
                height: 142px;
            }

            .smallItemSlot {
                width:  72px;
                height: 72px;
            }

            .weaponLeft {
                top: 100px; /* Adjust the vertical position */
                left: 17%; /* Center the item slot horizontally */
                transform: translateX(-50%); /* Adjust for centering */
            }

            .weaponRight {
                top: 100px; /* Adjust the vertical position */
                left: 83%; /* Center the item slot horizontally */
                transform: translateX(-50%); /* Adjust for centering */
            }

            .helmet {
                top: 20px; /* Adjust the vertical position */
                left: 50%; /* Center the item slot horizontally */
                transform: translateX(-50%); /* Adjust for centering */
            }

            .armor {
                top: 294px; /* Adjust the vertical position */
                left: 50%; /* Center the item slot horizontally */
                transform: translateX(-50%); /* Adjust for centering */
            }

            .belt {
                top: 536px; /* Adjust the vertical position */
                left: 50%; /* Center the item slot horizontally */
                transform: translateX(-50%); /* Adjust for centering */
            }

            .gloves {
                top: 444px; /* Adjust the vertical position */
                left: 17%; /* Center the item slot horizontally */
                transform: translateX(-50%); /* Adjust for centering */
            }

            .boots {
                top: 444px; /* Adjust the vertical position */
                left: 83%; /* Center the item slot horizontally */
                transform: translateX(-50%); /* Adjust for centering */
            }

            .amulet {
                top: 192px; /* Adjust the vertical position */
                left: 50%; /* Center the item slot horizontally */
                transform: translateX(-50%); /* Adjust for centering */
            }

            .ringLeft {
                top: 342px; /* Adjust the vertical position */
                left: 17%; /* Center the item slot horizontally */
                transform: translateX(-50%); /* Adjust for centering */
            }

            .ringRight {
                top: 342px; /* Adjust the vertical position */
                left: 83%; /* Center the item slot horizontally */
                transform: translateX(-50%); /* Adjust for centering */
            }


            .bagCanvas {
                background-color: var(--light--black--background-color);
                border: 1px solid black;
                width: 562px;
                height: 702px;
              }
              
              .gridContainer {
                display: grid;
                grid-template-columns: repeat(8, 1fr);
                grid-template-rows: repeat(10, 1fr);
              }
              
              .inventoryItemImage:hover {
                cursor: pointer;
              }
              
              .itemIcon {
                width: 100%;
                height: 100%;
                object-fit: contain;
              }

      
     