/* Transition effect container */
.transition-container {
    $teal: #8ba3b9;
  
    *,
    *:before,
    *:after {
      box-sizing: border-box;
    }
  
    html {
      overflow: hidden;
    }
  
    .overlay {
      position: absolute;
      top: 0;
      right: 0;
      bottom: 0;
      left: 0;
      pointer-events: none; /* Make the overlay non-clickable */
  
      &.show {
        .bar {
          transform: scale(1, 1) translate3d(0, 0, 0);
          transform-origin: 0 0;
        }
      }
  
      .bar {
        position: absolute;
        width: 25%; /* Adjust the width to 25% to cover the entire screen with 4 stripes */
        top: 0;
        bottom: 0;
        transform: scale(0, 1) translate3d(0, 0, 0);
        transform-origin: 100% 100%;
        transition: transform 0.5s ease-in-out;
        border: 3px solid black;
        &:first-of-type {
          border-left: none;
        }
      }
  
      /* Define individual classes for each stripe with different background images */
      .stripe1 {
        left: 0%;
        background: url('../../Images/FightScreen/NetherworldHarbinger.png');
        background-size: cover; /* Ensures the image covers the entire stripe */
        background-position: center; /* Center the image within the stripe */
      }
  
      .stripe2 {
        left: 25%;
        background: url('../../Images/FightScreen/VoidspawnLeviathan.png');
        background-size: cover;
        background-position: center;
      }
  
      .stripe3 {
        left: 50%;
        background: url('../../Images/FightScreen/ShadowfangWraith.png');
        background-size: cover;
        background-position: center;
      }
  
      .stripe4 {
        left: 75%;
        background: url('../../Images/FightScreen/SoulflameRevenant.png');
        background-size: cover;
        background-position: center;
      }
    }
  }
  