.gridSquare {
    background-color: rgba(51, 51, 51, 0.274);
    border: 1px solid rgba(0, 0, 0, 0.637);
    width: 70px;
    height: 70px;
  }
  
.gridSquare:hover {
    background-color: rgba(0, 130, 0, 0.2);
}

.gridSquare_highlighted {
    background-color: rgba(0, 130, 0, 0.5); /* Adjust the color for highlighting */
}