@import 'variables.css';

.containerBackground {
    background-image: url("../../Images/Backgrounds/KnightBackgroundDark.jpg");
    background-size: cover;
    background-repeat: no-repeat;
    background-attachment: fixed;
    
    position: absolute;
    left: 50%;
    transform: translate(-50%, 0); /* Center horizontally, no vertical shift */

    min-height: 100%; /* Adjust the height as needed */
    min-width: 100%; /* Adjust the width as needed */
}

    .containerHeroView {
        background-color: var(--light--grey--background-color);

        padding: 10px;
        width: 1356px;
        height: 792px;

        margin: 0 auto; /* Center the container horizontally */
        border-radius: 10px;
    }

        .containerHorizontal {
            color: var(--white-font-color);
            font-family: var(--text-font), var(--text-font-family);

            display: flex;
            justify-content: center;

            border-radius: 10px;

            margin-bottom: 10px;
        }

        .containerHorizontalTop {
            width: 1336px;
            height: 60px;
        }

            .containerHeroName{
                background-color: var(--light--black--background-color);

                font-size: 40px;
                
                border-radius: 10px;
                border: 1px solid black;

                margin-right: 4px;
                
                width: 468px;
            }

                .containerHeroName h2{
                    text-align: center;
                    
                    padding: 10px;
                    font-size: 40px;
                    line-height: 40px;
                }

            .containerCurrency {
                background-color: var(--light--black--background-color);    
                border-radius: 10px;
                border: 1px solid black;
                width: 562px;
                display: flex;
                justify-content: space-between; /* Distribute items with space in between */

                margin-right: 4px;

                padding-left: 100px;
                padding-right: 100px;
            }
                    
                .currencyItem {
                    display: flex; /* Display items horizontally */
                    align-items: center; /* Center vertically */
                }

                .goldIcon,
                .diamondIcon {
                    border-radius: 20px;
                    border: 1px solid black;
                    width: 55px; /* Adjust the width and height for your icons */
                    height: 55px;
                    margin-right: 10px; /* Add spacing between the icon and the amount */
                }
                
                .currencyAmount {
                    font-size: 24px; /* Adjust the font size for the currency amount */
                }

            .containerShopkeeperName {
                background-color: var(--light--black--background-color);
                font-size: 40px;
                border-radius: 10px;
                border: 1px solid black;
                width: 298px;
                height: 100px;
            }
                
                .containerShopkeeperName h2{
                    text-align: center;
                    
                    padding: 10px;
                    font-size: 40px;
                    line-height: 40px;
                }
            
        .containerHorizontalBottom {
            width: 1336px;
            height: 702px;
        }

            .equipCanvas {
                background-image: url('../../Images/HeroScreen/HeroDark.png');
                background-size: cover;
                
                margin-right: 4px; /* Adjust margin */

                border-radius: 10px;
                border: 1px solid black; /* Set the border color for monsterContainer to red */

                width: 468px;
                height: 702px;
            }

                .slotsHolder {
                    position: relative; /* Add relative positioning to the equipCanvas */
                }

                .itemSlot {
                    background-color: rgba(27, 27, 27, 0.603);
                    border: 1px solid white;
                    position: absolute; /* Set the item slot as absolute positioning */
                }

                /* Extend the common class for different sizes */
                .bigItemSlot {
                    width:  142px;
                    height: 212px;
                }

                .mediumItemSlot {
                    width:  142px;
                    height: 142px;
                }

                .smallItemSlot {
                    width:  72px;
                    height: 72px;
                }

                .weaponLeft {
                    top: 100px; /* Adjust the vertical position */
                    left: 17%; /* Center the item slot horizontally */
                    transform: translateX(-50%); /* Adjust for centering */
                }

                .weaponRight {
                    top: 100px; /* Adjust the vertical position */
                    left: 83%; /* Center the item slot horizontally */
                    transform: translateX(-50%); /* Adjust for centering */
                }

                .helmet {
                    top: 20px; /* Adjust the vertical position */
                    left: 50%; /* Center the item slot horizontally */
                    transform: translateX(-50%); /* Adjust for centering */
                }

                .armor {
                    top: 294px; /* Adjust the vertical position */
                    left: 50%; /* Center the item slot horizontally */
                    transform: translateX(-50%); /* Adjust for centering */
                }

                .belt {
                    top: 536px; /* Adjust the vertical position */
                    left: 50%; /* Center the item slot horizontally */
                    transform: translateX(-50%); /* Adjust for centering */
                }

                .gloves {
                    top: 444px; /* Adjust the vertical position */
                    left: 17%; /* Center the item slot horizontally */
                    transform: translateX(-50%); /* Adjust for centering */
                }

                .boots {
                    top: 444px; /* Adjust the vertical position */
                    left: 83%; /* Center the item slot horizontally */
                    transform: translateX(-50%); /* Adjust for centering */
                }

                .amulet {
                    top: 192px; /* Adjust the vertical position */
                    left: 50%; /* Center the item slot horizontally */
                    transform: translateX(-50%); /* Adjust for centering */
                }

                .ringLeft {
                    top: 342px; /* Adjust the vertical position */
                    left: 17%; /* Center the item slot horizontally */
                    transform: translateX(-50%); /* Adjust for centering */
                }

                .ringRight {
                    top: 342px; /* Adjust the vertical position */
                    left: 83%; /* Center the item slot horizontally */
                    transform: translateX(-50%); /* Adjust for centering */
                }


            .bagCanvas {
                background-color: var(--light--black--background-color);
                border: 1px solid black;

                margin-right: 4px; /* Adjust margin */

                width: 562px;
                height: 702px;
            }
              
              .gridContainer {
                display: grid;
                grid-template-columns: repeat(8, 1fr);
                grid-template-rows: repeat(10, 1fr);
              }
              
              .inventoryItemImage:hover {
                cursor: pointer;
              }
              
              .itemIcon {
                width: 100%;
                height: 100%;
                object-fit: contain;
              }


            .shopkeeperCanvas {
                margin-top: 40px; /* Adjust margin */

                width: 298px;
                height: 662px; /* 702 - 40 */
            }
            
                .shopkeeperCanvasImage {
                    background-image: url('../../Images/Shop/GribblenogBubblesnarl.png');
                    background-size: cover;
                    background-position: center;
            
                    border-radius: 10px;
                    border: 1px solid black; 

                    width: 298px;
                    height: 447px;
                }

                .sellAnimation {
                    position: absolute;
                    padding: 5px;
                    font-size: 22px; 
                    color: rgba(37, 207, 31, 0.8);
                    z-index: 100;
                    background-color: rgba(0, 0, 0, 0.8); 
                    border-radius: 8px;
                    animation: slideUp 10.0s ease-out forwards; /* Dodanie animacji slideUp */
                }

                .buyAnimation {
                    position: absolute;
                    padding: 5px;
                    font-size: 22px; 
                    color: rgba(207, 31, 31, 0.8);
                    z-index: 100;
                    background-color: rgba(0, 0, 0, 0.8); 
                    border-radius: 8px;
                    animation: slideUp 10.0s ease-out forwards; /* Dodanie animacji slideUp */
                }
            
                @keyframes slideUp {
                    0% {
                        transform: translateY(0);
                        opacity: 1;
                    }
                    100% {
                        transform: translateY(-30px); /* Przesunięcie o 30px w górę */
                        opacity: 0; 
                    }
                }
            
                .sellAnimation img, .buyAnimation img {
                    width: 28px;
                    height: 28px;
                    margin-bottom: 4px; /* Add space between text and image */
                }

                .shopkeeperCanvasText, .potionPanel {
                    background-color: var(--light--black--background-color);
                    border-radius: 10px;
                    border: 1px solid black; 

                    margin-top: 10px; /* Adjust margin */

                    width: 298px;
                    height: 205px;
                }
      
                .shopkeeperCanvasText h2, .shopkeeperCanvasText h3, .potionPanel button {
                    text-align: center;
                }
                  
                  .shopkeeperCanvasText h2 {
                    padding: 5px;
                    font-size: 25px;
                    line-height: 25px;
                  }
                  
                  .shopkeeperCanvasText h3 {
                    padding: 8px;
                    font-size: 20px;
                    line-height: 20px;
                  }
                  
                  .potionPanel {
                    display: flex;
                    justify-content: space-around;
                    align-items: center;
                  }
                  
                  .potion {
                    display: flex;
                    flex-direction: column;
                    align-items: center;
                  }
                  
                  .potionImage {
                    width: 78px;
                    height: 156px;
                  }
                  
                  .potion button {
                    background-color: var(--light--black--background-color);
                    color: var(--white-font-color);

                    width: 95px;
                    height: 35px;

                    font-size: 22px;
                    cursor: pointer;

                    border: 1px solid black; 
                    border-radius: 10px;
                  }

                  .coinIcon {
                    width: 24px;
                    height: 24px;
                    margin-bottom: 4px;
                  }