:root {
    --light--grey--background-color: #cecece63;
    --dark--grey--background-color: #a0a0a07e;
    --light--black--background-color: #00000071;
    --dark--black--background-color: #000000ab;

    --font-family: 'Times New Roman', serif;
    --white-font-color: #ffffff;
    --red-font-color: #a30000;
    --green-font-color: #30fd7e;
}

a {
    text-decoration: none;
    color: #007bff;
    font-weight: bold;
}

a:hover {
    text-decoration: underline;
}