@import 'variables.css';

.containerBackground {
    background-image: url("../../Images/Backgrounds/KnightBackgroundDark.jpg");
    background-size: cover; /* Adjust this as needed */
    background-repeat: no-repeat;
    background-attachment: fixed; /* Ensures the background remains fixed while scrolling */
    
    position: absolute;
    left: 50%;
    transform: translate(-50%, 0); /* Center horizontally, no vertical shift */

    min-height: 100%; /* Adjust the height as needed */
    min-width: 100%; /* Adjust the width as needed */
}

* {
    font-family: var(--font-family);
}

.container {
    background-color: var(--light--grey--background-color); /* Gray semi-transparent background */

    display: flex;
    justify-content: center; /* Center horizontally */
    
    position: absolute;
    left: 50%;
    transform: translate(-50%, 0); /* Center horizontally, no vertical shift */
    
    padding: 10px;

    min-height: 40%; /* Adjust the height as needed */
    min-width: 35%; /* Adjust the width as needed */

    border-radius: 15px;
}


.dashboardContainer {
    background-color: var(--dark--grey--background-color);

    padding: 20px;

    width: 100%; /* Make it fully fill the .container horizontally */

    border-radius: 15px;
}


.errorMessageContainer {
    color: var(--red-font-color);
    font-weight: bold;
    white-space: pre-line;
}

.successMessageContainer {
    color: var(--green-font-color);
    font-weight: bold;
    white-space: pre-line;
}

.dashboardContainer h1 {
    font-size: 28px;
    text-align: center;
    font-weight: bold;
}

.dashboardContainer p {
    font-size: 16px;
    text-align: center;
}

.centerButton {
    display: flex;
    justify-content: center; /* Center horizontally */
}