@import 'variables.css';

html {
    font-size: 1vw; /* 1% of the viewport width */
}


.containerBackground {
    background-image: url("../../Images/Backgrounds/MonsterBackgroundDark.jpg");
    background-size: cover;
    background-repeat: no-repeat;
    background-attachment: fixed;

    position: absolute;
    left: 50%;
    transform: translate(-50%, 0);

    min-width: 100%;
    width: 100%;
    min-height: 100%;
    height: 100%;

    padding-left: 11%;
    padding-right: 11%;
    display: flex;
    justify-content: space-between; /* Distribute space between buttons */
    align-items: center; /* Center vertically */
}

.fightContainer {
    background-color: var(--light--grey--background-color);

    padding: 6px;
    border-radius: 5px;

    width: 848px;
    height: 865px;
}

    .heroMonsterContainer {
        display: flex; /* Use flex container for hero and monster */

        gap: 16px;

        width: 836px;
        height: 773px;
    }

    .heroContainer,
    .monsterContainer {
        background-color: var(--light--black--background-color);

        padding: 10px;
        border-radius: 10px;

        align-items: center; /* Center the text horizontally */
        text-align: center; /* Center the text horizontally within the container */

        border: 1px solid rgb(0, 0, 0); /* Set the border color for heroContainer to green */
        
        width: 410px;
        height: 773px;
    }

        .heroHeader,
        .monsterHeader {
            font-weight: bold;
            font-size: 1.8em; /* Use relative font size, adjust as needed */

            color: var(--white-font-color);
            background-color: var(--light--black--background-color);

            width: 388px;
            height: 50px;

            border-radius: 5px;

            text-align: center;
        }

        .heroImage,
        .monsterImage {
            width: 388px;
            height: 570px; 
            
            border-radius: 10px;
            margin-top: 6px;
            margin-bottom: 6px;
        }

        .heroImage {
            border: 2px solid green; /* Set the border color for heroContainer to green */
        }

        .monsterImage {
            border: 2px solid rgb(129, 0, 0); /* Set the border color for monsterContainer to red */
        }

    .heroStats,
    .monsterStats {
        font-size: 2em; 
    }

    .statsCanvas {
        background-color: var(--light--black--background-color);
        color: white;
        padding: 1%;
        padding-left: 4%;
        padding-right: 4%;
        border-radius: 10px;

        width: 388px;
        height: 119px;
    }

        .stat {
            display: flex;
            justify-content: space-between; /* Create space between name and value */
            margin-bottom: -2px;
        }

        .statName {
            text-align: left; /* Align the name to the left */
        }

        .statValue {
            text-align: right; /* Align the value to the right */
        }

.battleActionsCanvas {
    background-color: var(--dark--black--background-color);

    margin-top: 6px;
    padding: 10px;
    border-radius: 5px;

    min-width: 100%; /* Make the image width responsive to the parent container */
    width: 100%;
    height: 74px; /* Set the height as a percentage of its parent */

    display: flex;
    justify-content: space-between; /* Distribute space between buttons */
    align-items: center; /* Center vertically */
}

    .button {
        color: #fff;
        border: none;

        font-size: 2em; /* Use relative font size, adjust as needed */

        height: 100%; /* Set the height to your desired value as a percentage */

        border-radius: 5px;
        cursor: pointer;
    }
    
    .retreatButton {
        background-color: #bb0000; /* Red color for the Retreat button */
        width: 398px; 
        height: 54px; 
    }

    .attackButton {
        background-color: #007bff; /* Blue color for the Attack button */
        width: 193px; 
        height: 54px; 
    }

    .autoButton {
        background-color: #1c8612; /* Blue color for the Attack button */
        width: 193px; 
        height: 54px; 
    }
    
    .leaveButton {
        background-color: #2cbb00; /* Red color for the Retreat button */
        width: 100%; /* Set the width to your desired value */

        display: flex;
        justify-content: center; /* Center the content horizontally */
        align-items: center; /* Center the content vertically */
    }

.fightLog {
    background-color: var(--light--grey--background-color);
    border: 1px solid #d1d1d1;
    border-radius: 5px;
    overflow-y: scroll;
    max-width: 41%; /* Make the image width responsive to the parent container */
    width: 41%;
    min-height: 865px; /* Adjust the minimum height as needed */
    max-height: 865px; /* Adjust the maximum height as needed */
    overflow-y: auto;
    display: flex;
    flex-direction: column; /* Reverse the column direction for newer messages at the bottom */

    padding: 2px;
}

    .messageContainer {
        min-width: 100%;
        max-width: 100%;
        border: 1px solid #000000;
        border-radius: 5px; /* Add rounded corners */
        display: flex;
        justify-content: flex-start;
        white-space: pre-line; /* Preserve spaces and line breaks */
        margin-bottom: 1px; /* Adjust as needed to control spacing between messages */
    }

    .messageContainer_start {
        color: var(--white-font-color);
        background-color: #a1a1a1; /* Set the background color for server battle messages */
    }

    .messageContainer_ally {
        color: var(--white-font-color);
        background-color: #257e2d; /* Set the background color for server battle messages */
    }

    .messageContainer_enemy {
        color: var(--white-font-color);
        background-color: #852525; /* Set the background color for server battle messages */
    }

    .messageContainer_end {
        color: var(--white-font-color);
        background-color: #1f1f1f; /* Set the background color for server battle messages */
    }

    .messageContainer_exp_reward {
        color: var(--white-font-color);
        background-color: #1355a1; /* Set the background color for server battle messages */
    }

    .messageContainer_gold_reward {
        color: var(--white-font-color);
        background-color: #a19f13; /* Set the background color for server battle messages */
    }

    .messageContainer_item_reward {
        color: var(--white-font-color);
        background-color: #a19f13; /* Set the background color for server battle messages */
    }

    .messageContainer_none {
        color: var(--white-font-color);
        background-color: var(--dark--grey--background-color); /* Background color of the message container */
    }

    .message {
        padding: 8px; /* Adjust padding as needed */
        word-wrap: break-word; /* Allow long words to wrap */
    }