/* Home.css */
@import 'variables.css';

.containerBackground {
    background-image: url("../../Images/Backgrounds/KnightBackgroundDark.jpg");
    background-size: cover; /* Adjust this as needed */
    background-repeat: no-repeat;
    background-attachment: fixed; /* Ensures the background remains fixed while scrolling */
    
    position: absolute;
    left: 50%;
    transform: translate(-50%, 0); /* Center horizontally, no vertical shift */

    min-height: 100%; /* Adjust the height as needed */
    min-width: 100%; /* Adjust the width as needed */
}

* {
    font-family: var(--font-family);
}

.container {
    background-color: #cececeb2; /* Gray semi-transparent background */
    height: 40vh;
    display: flex;
    flex-direction: column;
    justify-content: center; /* Center vertically */
    align-items: center; /* Center horizontally */
    text-align: center; /* Center the text */
}

.lead {
    font-size: 20px; /* Adjust the font size as needed */
}

.customButton {
    margin-left: 10px; /* Adjust the margin as needed */
}

.customButton {
    margin-right: 10px; /* Adjust the margin as needed */
}
